import React from "react";
import { Button, Col, Row } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

import "../styles/orderplaced.css";

const OrderPlacedScreen = () => {
  const history = useNavigate();
  const ShopMoreHandler = () => {
    history("/product");
  };
  const OrderDetailHandler = () => {
    history("/profile");
  };
  return (
    <div>
      <Row className="bgimg">
        <Col>
          <div>
            <img
              src="https://i.postimg.cc/4x8pb8TY/7191129-3584066.jpg"
              style={{ height: "30rem" }}
              alt=""
            />
          </div>
        </Col>
        <Col>
          <div className="pb-5">
            <h1 className="pb-0 py-5">Thank You!</h1>
            <span className="ps-5 py-0">for purchasing products</span>
          </div>
          <div className="p-2">
            <Button
              variant="flush"
              onClick={OrderDetailHandler}
              className=" btn-outline-info"
            >
              Order Details
            </Button>
          </div>
          <div className="p-2 ps-4">
            <Button
              className="btn-outline-success"
              onClick={ShopMoreHandler}
              variant="flush"
            >
              Shop More
            </Button>
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default OrderPlacedScreen;
